import './App.css';
import React from 'react';
import { Link } from "react-router-dom";

class Header extends React.Component{
  constructor(props){
    super(props);
    this.state = {isToggleOn: false}
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick(){
    this.setState({
      isToggleOn: !this.state.isToggleOn
    })
  }
render(){
return(
    <div className="header">
      <div className="container">
        <Link to="/" className="header-image"><img src={this.props.image} alt="sirokuma"/></Link>
        <h1 className="header-title">ポートフォリオ</h1>
        <div id="navArea" className={this.state.isToggleOn ? "open" : ""}>
          <nav>
            <div className="inner">
              <ul>
                <li><Link to="/">ホーム</Link></li>
                <li><Link to="/about">自己紹介</Link></li>
                <li><Link to="/do">現状と目標</Link></li>
                <li><Link to="/hobby">趣味</Link></li>
                <li><Link to="/opus">作品</Link></li>
              </ul>
            </div>
          </nav>

          <div className="toggle_btn" onClick={this.handleClick}>
            <span></span>
            <span></span>
            <span></span>
          </div>

          <div id="mask" onClick={this.handleClick}></div>
        </div>
      </div>
    </div>
)
}
}

export default Header;